import { useState } from "react";
import "./App.css";
import AppHeader from "./components/AppHeader";
import ImageItem from "./components/ImageItem";
import ImagePost from "./components/ImagePost";
import imageData from "./data/imageData";

function App() {
  const [selectedImage, setSelectedImage] = useState(null);

  function onImageOpenClick(theImage) {
    setSelectedImage(theImage);
  }

  function onImageCloseClick() {
    setSelectedImage(null);
  }

  const imageElement = imageData.map((image, index) => {
    return <ImageItem key={index} myimage={image} onImageClick={onImageOpenClick}/>;
  });

  let imagePost = null;
  if (!!selectedImage) {
    imagePost = <ImagePost image={selectedImage} onBgClick={onImageCloseClick}/>;
  }
  
  return (
    <div className="App">
      <AppHeader />
      <div class="app-grid">{imageElement}</div>
      {imagePost}
    </div>
  );
}

export default App;
