import "./ImageItem.css";

function ImageItem(props) {
  const { myimage,onImageClick } = props;
  console.log(props.myimage)
  return (
    <div>
      <img className="image-item" src={myimage.thumbnaiUrl} onClick={() => {onImageClick(myimage)}}/>
      <h4 style={{textAlign:"center"}}>{myimage.title}</h4>
    </div>
  );
}

export default ImageItem;
